.main-container {
  display: flex;
  height: 100vh;
}

.login-left {
  padding: 1rem;
}

.login-right {
  background-color: #f4f4ff;
  border-top-left-radius: 2rem;
  border-bottom-left-radius: 2rem;
}

.logo {
  border-right: 1px solid #ebebeb;
  padding-right: 0.8rem;
  margin-right: 1rem;
}

.login-content {
  justify-content: center;
  align-items: center;
  display: flex;
  height: 90%;
  flex-direction: column;
}

.login-form {
  width: 65%;
}

.ant-form-item-label label,
.sign-txt {
  font-family: "Poppins";
}

.ant-input, .ant-input-password {
  padding: 0.7rem;
}
.ant-form-item-label {
  font-size: 1rem;
}
.title {
  margin-bottom: 8%;
  color: #514F4F;
  font-family: 'Poppins';
  font-size: 1.8rem;
}

.signin-btn {
  display: flex;
  justify-content: center;
  position: relative;
  cursor: pointer;
  margin-bottom: 1rem;
}

.signin-btn img {
  margin-left: -2px;
}

.sign-txt {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-weight: 500;
  font-size: 1rem;
}

.login-info {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
  align-items: center;
}

.login-info div img {
  width: 100%;
  align-self: flex-end;
  height: 49vh;
}

.slider {
  width: 100%;
  padding: 8%;
  line-height: 3rem;
  height: 50%;
}

.descrption {
  font-family: 'Playfair Display', serif;
}

.author {
  text-align: right;
  font-family: 'Poppins';
  color: #706C6C;
  font-size: 0.9rem;
  margin-bottom: 3%;
}

.slick-dots li button:before {
  font-size: 15px;
}

.slick-dots li.slick-active button:before  {
  color: #5858C6;
}

.tooltext {
  text-align: right;
  padding: 0.6rem 0;
  cursor: pointer;
  font-family: 'Poppins';
}

.text {
  font-family: 'Poppins';
  font-size: 0.8rem;
  text-align: center;

}

.link {
  cursor: pointer;
  color: #2082F4;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}